.editions {
  width: 100%;
  min-height: vw(763);
  padding: vw(80);

  background-image: url('../img/editions/bg.png');
  background-repeat: no-repeat;
  background-size: cover;
  background-color: black;

  @include medium {
    min-height: md-vw(763);
    padding: md-vw(80) 0;
  }
}

.editions__container {
  display: flex;
  align-items: flex-start;
}

.editions__left-group {
  margin-right: vw(173);

  @include medium {
    margin-right: md-vw(18);
  }
}

.editions__heading {
  color: var(--color-text-editions);

  margin-bottom: vw(18);

  @include medium {
    margin-bottom: md-vw(18);
  }
}

.editions__list-name {
  font-weight: 700;
  font-size: vw(16);
  line-height: vw(32);
  color: var(--color-text-editions);

  margin-bottom: vw(9);

  @include medium {
    font-size: md-vw(16);
    line-height: md-vw(32);

    margin-bottom: md-vw(9);
  }
}

.editions__list {
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  margin-bottom: vw(32);

  @include medium {
    margin-bottom: md-vw(32);
  }
}

.editions__check {
  display: flex;
  align-items: center;

  cursor: pointer;

  input:checked + span {
    border-color: var(--color-active-editions);

    &::after {
      opacity: 1;
    }
  }

  input:checked + span + strong {
    color: var(--color-active-editions);
  }


  span {
    position: relative;
    width: vw(12);
    height: vw(12);
    border: vw(1) solid var(--color-text-editions);

    margin-right: vw(12);

    transition: border-color .15s ease;

    @include medium {
      width: md-vw(12);
      height: md-vw(12);
      border: md-vw(1) solid var(--color-text-editions);

      margin-right: md-vw(12);
    }

    &::after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);

      width: vw(4);
      height: vw(4);

      border-radius: 50%;
      background: var(--color-active-editions);

      opacity: 0;
      transition: opacity .15s ease;

      @include medium {
        width: md-vw(4);
        height: md-vw(4);
      }
    }
  }

  strong {
    font-size: vw(16);
    line-height: vw(30);
    color: var(--color-text-editions);

    transition: color .15s ease;

    @include medium {
      font-size: md-vw(16);
      line-height: md-vw(30);
    }
  }

  &:not(:last-child) {
    margin-bottom: vw(1.75);

    @include medium {
      margin-bottom: md-vw(1.75);
    }
  }
}


.editions__price-name {
  display: block;

  font-weight: 700;
  font-size: vw(16);
  line-height: vw(32);
  color: var(--color-text-editions);

  margin-bottom: vw(10);

  @include medium {
    font-size: md-vw(16);
    line-height: md-vw(32);

    margin-bottom: md-vw(10);
  }
}

.editions__price {
  display: inline-flex;
  align-items: center;
  justify-content: flex-start;

  min-width: vw(277);
  height: vw(40);

  border: vw(1) solid var(--color-price-border-editions);
  border-radius: vw(6);

  @include medium {
    min-width: md-vw(277);
    height: md-vw(40);

    border: md-vw(1) solid var(--color-price-border-editions);
    border-radius: md-vw(6);
  }
}

.editions__price__number {
  height: 100%;

  input {
    width: vw(73);
    height: 100%;
    padding: vw(4) vw(10);
    border: none;
    background: var(--color-price-bg-editions);

    font-size: vw(16);
    line-height: vw(32);
    color: var(--color-text-editions);

    outline: none;

    @include medium {
      width: md-vw(73);
      padding: md-vw(4) md-vw(10);

      font-size: md-vw(16);
      line-height: md-vw(32);
    }
  }

  span {
    font-size: vw(16);
    line-height: vw(32);
    color: var(--color-text-editions);

    padding: 0 vw(5) 0 vw(10);

    @include medium {
      font-size: md-vw(16);
      line-height: md-vw(32);

      padding: 0 md-vw(5) 0 md-vw(10);
    }
  }
}

.editions__price__span {
  font-size: vw(16);
  line-height: vw(32);
  color: var(--color-text-editions);

  padding-left: vw(10);
  
  @include medium {
    font-size: md-vw(16);
    line-height: md-vw(32);

    padding-left: md-vw(10);
  }
}



.editions__right-group {
  position: relative;
  padding-top: vw(93);

  width: 100%;

  @include medium {
    padding-top: md-vw(93);
  }
}

.editions__swiper-container {
  width: 100%;
  max-width: vw(1150);
  height: 100%;

  @include medium {
    max-width: md-vw(630);
  }
}

.editions__swiper-button-prev,
.editions__swiper-button-next {
  position: absolute;
  top: 0;
  left: 0;
  min-width: vw(30);
  width: vw(30);
  min-height: vw(30);
  height: vw(30);
  background: var(--color-arrow-editions);
  border-radius: 50%;
  z-index: 1000;
  cursor: pointer;

  &::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-35%, -50%) rotate(45deg);
    width: vw(8);
    height: vw(8);

    border-bottom: vw(1) solid var(--color-text-editions);
    border-left: vw(1) solid var(--color-text-editions);

    @include medium {
      width: md-vw(8);
      height: md-vw(8);

      border-bottom: md-vw(1) solid var(--color-text-editions);
      border-left: md-vw(1) solid var(--color-text-editions);
    }
  }

  @include medium {
    min-width: md-vw(30);
    width: md-vw(30);
    min-height: md-vw(30);
    height: md-vw(30);
  }
}

.editions__swiper-button-next {
  left: vw(100);

  &::after {
    transform: translate(-65%, -50%) rotate(-135deg);
  }

  @include medium {
    left: md-vw(100);
  }
}

.editions__swiper__count {
  position: absolute;
  top: 0;
  left: vw(50);
  font-size: vw(16);
  line-height: vw(32);
  color: var(--color-text-editions);

  @include medium {
    left: md-vw(50);
    font-size: md-vw(16);
    line-height: md-vw(32);
  }
}

.editions__swiper-slide {
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  width: vw(350);

  @include medium {
    width: md-vw(290);
  }
}

.editions__swiper-img {
  width: 100%;
  height: vw(390);

  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;

  margin-bottom: vw(23);

  @include medium {
    height: md-vw(320);

    margin-bottom: md-vw(13);
  }
}

.editions__swiper-img.eb-1 {
  background-image: url('../img/editions/1.png');
}
.editions__swiper-img.eb-2 {
  background-image: url('../img/editions/2.png');
}
.editions__swiper-img.eb-3 {
  background-image: url('../img/editions/3.png');
}
.editions__swiper-img.eb-4 {
  background-image: url('../img/editions/4.png');
}
.editions__swiper-img.eb-5 {
  background-image: url('../img/editions/5.png');
}
.editions__swiper-img.eb-6 {
  background-image: url('../img/editions/6.png');
}
.editions__swiper-img.eb-7 {
  background-image: url('../img/editions/7.png');
}

.editions__swiper-heading {
  font-weight: 700;
  font-size: vw(16);
  line-height: vw(22);
  color: var(--color-text-editions);

  @include medium {
    font-size: md-vw(16);
    line-height: md-vw(22);
  }
}

.editions__swiper-desc {
  font-size: vw(16);
  line-height: vw(22);
  color: var(--color-text-editions);

  margin-bottom: vw(20);

  @include medium {
    font-size: md-vw(16);
    line-height: md-vw(22);

    margin-bottom: md-vw(20);
  }
}

.editions__swiper-btn {
  font-weight: 700;
  font-size: vw(16);
  line-height: vw(22);
  color: var(--color-text-editions);

  padding: vw(8) vw(14);
  border-radius: vw(100);
  border: vw(2) solid var(--color-main);

  @include medium {
    font-size: md-vw(16);
    line-height: md-vw(22);

    padding: md-vw(8) md-vw(14);
    border-radius: md-vw(100);
    border: md-vw(2) solid var(--color-main);
  }
}

.editions__swiper-price {
  position: absolute;
  right: 0;
  top: vw(413);

  font-style: italic;
  font-size: vw(16);
  line-height: vw(22);
  color: var(--color-text-editions);
  text-align: right;

  @include medium {
    top: md-vw(413);

    font-style: italic;
    font-size: md-vw(16);
    line-height: md-vw(22);
  }
}