// CONTAINER
.container {
  // 1920px
  max-width: vw(1630); //1600px
  padding: 0 vw(15);
  margin: 0 auto;

  // 1024px
  @include medium {
    max-width: md-vw(952); //922px
    padding: 0 md-vw(15);
  }

  // 768px 
  @include small {
    max-width: sm-vw(698); //668px
    padding: 0 sm-vw(15);
  }

  // 320px
  @include mobile {
    max-width: mb-vw(320); //290px
    padding: 0 mb-vw(15);
  }
}