.catalog {
  padding: vw(80) 0;

  @include medium {
    padding: md-vw(80) 0;
  }
}

.catalog__heading {
  margin-right: vw(30);

  @include medium {
    margin-right: md-vw(30);
  }
}

.catalog__desc {
  max-width: vw(1080);

  @include medium {
    max-width: md-vw(1080);
  }
}

.catalog__top {
  display: flex;
  align-items: center;
  flex-wrap: wrap;

  margin-bottom: vw(50);

  @include medium {
    margin-bottom: md-vw(50);
  }
}

.catalog__lang__list {
  display: flex;
  align-items: center;

  margin-bottom: vw(30);

  @include medium {
    margin-bottom: md-vw(30);
  }
}

.catalog__lang__item {
  width: vw(66);
  height: vw(66);

  background-repeat:no-repeat;
  background-size: vw(46) vw(46);
  background-position: center center;

  border-radius: 50%;
  padding: vw(10);
  cursor: pointer;

  &:not(:last-child) {
    margin-right: vw(30);

    @include medium {
      margin-right: md-vw(30);
    }
  }

  @include medium {
    width: md-vw(66);
    height: md-vw(66);

    background-size: md-vw(46) md-vw(46);
    padding: md-vw(10);
  }
}

.catalog__lang__item.is_active {
  box-shadow: vw(0) vw(0) vw(0) vw(2) var(--color-active-line-catalog);

  @include medium {
    box-shadow: md-vw(0) md-vw(0) md-vw(0) md-vw(2) var(--color-active-line-catalog);
  }
}

.catalog__lang__item:nth-child(1) {
  background-image: url('../img/catalog/fr.png');
}
.catalog__lang__item:nth-child(2) {
  background-image: url('../img/catalog/gr.png');
}
.catalog__lang__item:nth-child(3) {
  background-image: url('../img/catalog/it.png');
}
.catalog__lang__item:nth-child(4) {
  background-image: url('../img/catalog/ru.png');
}
.catalog__lang__item:nth-child(5) {
  background-image: url('../img/catalog/bg.png');
}




.catalog__lang-content {
  position: relative;
}

.catolog__lang-content__item {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  align-items: flex-start;

  opacity: 0;
  transition: opacity .3s ease;
}

.catolog__lang-content__item.is_active {
  opacity: 1;
}

.catalog__lang-content__item__left {
  position: absolute;
  top: 0;
  left: 0;
  max-width: vw(750);
  margin-right: vw(50);

  opacity: 0;
  visibility: hidden;
  transition: opacity .7s ease-in-out, visibility 1s ease-in-out;
  z-index: 1;

  @include medium {
    max-width: md-vw(437);
    margin-right: md-vw(50);
  }
}

.catalog__lang-content__item__left.is_active {
  opacity: 1;
  visibility: visible;
  z-index: 100;
}

.catalog__lang-content__item__left__img {
  width: 100%;
  height: vw(470);

  background: url('../img/catalog/1.png') no-repeat;
  background-size: cover;
  background-position: center center;

  margin-bottom: vw(30);

  @include medium {
    height: md-vw(274);

    margin-bottom: md-vw(30);
  }
}

.catalog__lang-content__item__left__heading {
  font-weight: 600;
  font-size: vw(24);
  line-height: vw(33);
  color: var(--color-text-catalog);

  margin-bottom: vw(15);

  @include medium {
    font-size: md-vw(24);
    line-height: md-vw(33);

    margin-bottom: md-vw(15);
  }
}

.catalog__lang-content__item__left__desc {
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  font-size: vw(16);
  line-height: vw(32);
  color: var(--color-text-catalog);

  @include medium {
    font-size: md-vw(16);
    line-height: md-vw(32);
  }
}

.catalog__lang-content__item__left__desc__date {
  font-size: vw(12);
  line-height: vw(16);
  color: var(--color-date-catalog);
  
  @include medium {
    font-size: md-vw(12);
    line-height: md-vw(16);
  }
}



.catalog__lang-content__item__right {
  position: absolute;
  top: 0;
  right: 0;
}

.catalog__accordion {
  width: 100%;
}

.catalog__accordion__heading {
  display: block;
  padding: vw(41) 0;

  font-weight: 600;
  font-size: vw(24);
  line-height: vw(32);
  color: var(--color-text-catalog);
  cursor: pointer;

  border-top: vw(1) solid var(--color-line-catalog);

  @include medium {
    padding: md-vw(41) 0;
    font-size: md-vw(24);
    line-height: md-vw(32);
    border-top: md-vw(1) solid var(--color-line-catalog);
  }
}

.catalog__accordion__arts {
  box-sizing: content-box;
  column-count: 3;

  max-width: vw(800);
  height: vw(338);
  max-height: vw(288);
  padding: vw(25) 0;

  &:last-child {
    border-bottom: vw(1) solid var(--color-line-catalog);

    @include medium {
      border-bottom: md-vw(1) solid var(--color-line-catalog);
    }
  }

  @include medium {
    column-count: 2;
    max-width: md-vw(437);
    height: md-vw(374);
    max-height: md-vw(374);
    padding: md-vw(25) 0;
  }
}

.catalog__accordion__art {
  font-size: vw(16);
  line-height: vw(32);
  color: var(--color-text-catalog);

  &:nth-child(n+10) {
    margin-left: vw(60);

    @include medium {
      margin-left: 0;
    }
  }

  &:nth-child(n+12) {
    @include medium {
      margin-left: md-vw(35);
    }
  }

  @include medium {
    font-size: md-vw(16);
    line-height: md-vw(32);
  }
}