// COLORS
:root {
  --color-bg: white;
  --color-black: black;
  --color-main: #9D5CD0;

  // header
  --fill-svg-header: white;
  --color-bg-header: black;
  --color-text-header: white;

  // hero
  --color-bg-topbar-hero: rgba(0, 0, 0, .4);
  --color-bg-dropdown-hero: rgba(0, 0, 0, 0.6);
  --color-bg-scrollbar-hero: black;
  --fill-svg-hero: white;
  --color-search-hero: rgba(255, 255, 255, .85);
  --color-text-hero: white;

  // about-us
  --color-text-about-us: #333;

  // gallery
  --color-text-gallery: white;
  --color-arrow-gallery: #666;
  --color-auth-gallery: #C283F3;
  --color-bg-gallery: #090909;
  --color-border-select-gallery: #999;

  // catalog
  --color-text-catalog: #333;
  --color-date-catalog: #999;
  --color-arrow-catalog: #ECECEC;
  --color-active-arrow-catalog: #D1A9F0;
  --color-line-catalog: #CACACA;
  --color-active-line-catalog: #7943A4;
  --fill-art-catalog: #B6B6B6;

  // events
  --color-card-border-events: #CACACA;
  --color-text-events: #333;
  --color-where-events: #999;
  --color-more-events: #9D5CD0;

  // editions
  --color-text-editions: white;
  --color-active-editions: #C283F3;
  --color-price-border-editions: rgba(255, 255, 255, .5);
  --color-price-bg-editions: rgba(157, 92, 208, 0.5);
  --color-arrow-editions: #666;

  // projects
  --color-text-projects: #333;
  --color-tooltip-projects: white;
  --color-arrow-projects: #ECECEC;
  --color-card-border-projects: #CACACA;

  // contacts
  --color-text-contacts: #333;
  --color-placeholder-contacts: #999;
  --color-border-contacts: #CACACA;

  // footer
  --fill-logo-footer: white;
  --fill-svg-footer: #CACACA;
  --color-bg-footer: #000;
}