.contacts {
  position: relative;
}

.contacts__container {
  position: relative;
}

.contacts__map {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
}

[class*='ground-pane'] {
  filter: grayscale(1);
}

.contacts__content {
  position: relative;
  width: 100%;
  height: vw(700);

  @include medium {
    height: md-vw(700);
  }
}

.contacts__content-form {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;

  width: vw(682);

  background: var(--color-bg);
  padding: vw(0) vw(130) vw(0) vw(152);

  border-right: vw(1) solid var(--color-border-contacts);
  border-left: vw(1) solid var(--color-border-contacts);
  border-top: vw(1) solid var(--color-border-contacts);

  @include medium {
    width: md-vw(487);
    padding: md-vw(0) md-vw(67) md-vw(0) md-vw(50);

    border-right: md-vw(1) solid var(--color-border-contacts);
    border-left: md-vw(1) solid var(--color-border-contacts);
    border-top: md-vw(1) solid var(--color-border-contacts);
  }
}

.contacts__place-1 {
  padding-top: vw(124);

  font-weight: 600;
  font-size: vw(24);
  line-height: vw(32);
  color: var(--color-text-contacts);

  @include medium {
    padding-top: md-vw(124);

    font-size: md-vw(24);
    line-height: md-vw(32);
  }
}

.contacts__place-2 {
  font-size: vw(16);
  line-height: vw(32);
  color: var(--color-text-contacts);

  margin-bottom: vw(27);

  @include medium {
    font-size: md-vw(16);
    line-height: md-vw(32);

    margin-bottom: md-vw(27);
  }
}

.contacts__name-form {
  font-size: vw(24);
  line-height: vw(32);
  color: var(--color-text-contacts);

  margin-bottom: vw(19);

  @include medium {
    font-size: md-vw(24);
    line-height: md-vw(32);
  
    margin-bottom: md-vw(19);
  }
}

.contacts__form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.contacts__form__input {
  width: vw(400);
  height: vw(40);
  padding: vw(4) vw(24);
  border-radius: vw(100);
  border: vw(1) solid var(--color-text-contacts);
  outline: none;

  font-size: vw(16);
  line-height: vw(32);
  color: var(--color-text-contacts);

  @include medium {
    width: md-vw(335);
    height: md-vw(40);
    padding: md-vw(4) md-vw(24);
    border-radius: md-vw(100);
    border: md-vw(1) solid var(--color-text-contacts);
  
    font-size: md-vw(16);
    line-height: md-vw(32);
  }

  &::placeholder {
    color: var(--color-placeholder-contacts);
  }

  &:not(:last-child) {
    margin-bottom: vw(23);

    @include medium {
      margin-bottom: md-vw(23);
    }
  }
}

.contacts__form__btn {
  padding: vw(17) vw(35);
  font-weight: 700;
  font-size: vw(18);
  line-height: vw(25);
  color: var(--color-main);
  border-radius: vw(100);
  border: vw(2) solid var(--color-main);

  margin-bottom: vw(38);

  @include medium {
    padding: md-vw(17) md-vw(35);
    font-size: md-vw(18);
    line-height: md-vw(25);
    color: var(--color-main);
    border-radius: md-vw(100);
    border: md-vw(2) solid var(--color-main);

    margin-bottom: md-vw(38);
  }
}

.contacts__name-soc {
  font-size: vw(24);
  line-height: vw(32);
  color: var(--color-text-contacts);

  margin-bottom: vw(18);

  @include medium {
    font-size: md-vw(24);
    line-height: md-vw(32);
  
    margin-bottom: md-vw(18);
  }
}

.contacts__soc-list {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.contacts__soc-item {
  &:not(:last-child) {
    margin-right: vw(20);

    @include medium {
      margin-right: md-vw(20);
    }
  }
}

.contacts__soc-item__link {
  svg {
    width: vw(40);
    height: vw(40);

    @include medium {
      width: md-vw(60);
      height: md-vw(60);
    }
  }
}