.footer {
  width: 100%;
  height: vw(160);
  background: var(--color-bg-footer);

  @include medium {
    height: md-vw(140);
  }
}

.footer__container {
  display: flex;
  align-items: center;
  justify-content: space-between;

  height: 100%;
}

.footer__logo {
  svg {
    width: vw(173);
    height: vw(40);
    fill: var(--fill-logo-footer);

    @include medium {
      width: md-vw(173);
      height: md-vw(40);
    }
  }
}

.footer__list {
  display: flex;
  align-items: center;
}

.footer__item {
  &:not(:last-child) {
    margin-right: vw(20);

    @include medium {
      margin-right: md-vw(30);
    }
  }
}

.footer__item__link {
  svg {
    width: vw(45);
    height: vw(45);
    fill: var(--fill-svg-footer);

    @include medium {
      width: md-vw(60 );
      height: md-vw(60);
    }
  }
}