.gallery {
  width: 100%;
  height: 100%;
  height: vw(995);
  padding: vw(80) 0;
  background-color: var(--color-bg-gallery);
  background-image: url('../img/gallery/bg.png');
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: center;

  @include medium {
    height: md-vw(839);
    padding: md-vw(80) 0;
  }
}

.gallery__container {
  display: flex;

  height: 100%;
}

.gallery__left-group {
  position: relative;

  width: 100%;
  max-width: vw(400);
  height: 100%;
  min-height: vw(835);

  margin-right: vw(50);

  @include medium {
    max-width: md-vw(400);
    min-height: md-vw(679);

    margin-right: md-vw(50);
  }
}

.gallery__heading {
  color: var(--color-text-gallery);
}

.gallery__desc {
  position: absolute;
  left: 0;
  bottom: 0;

  max-width: vw(350);
  color: var(--color-text-gallery);

  a {
    color: var(--color-auth-gallery);
  }

  @include medium {
    max-width: md-vw(350);
  }
}

.gallery__filter {
  display: inline-block;

  font-weight: 600;
  font-size: vw(16);
  line-height: vw(32);
  color: var(--color-text-gallery);

  margin-bottom: vw(10);

  @include medium {
    font-size: md-vw(16);
    line-height: md-vw(32);

    margin-bottom: md-vw(10);
  }
}



.choices * {
  background: transparent;
}

.choices {
  margin-bottom: 0;
}

.choices__list--dropdown .choices__item {
  padding: 0;
}

.choices__item {
  font-size: vw(16);
  line-height: vw(32);
  color: var(--color-text-gallery);

  padding: 0 0 vw(5) vw(15) !important;

  @include medium {
    font-size: md-vw(16);
    line-height: md-vw(32);

    padding: 0 0 md-vw(5) md-vw(15) !important;
  }
}

.choices__item--selectable {
  border: 1px solid var(--color-text-gallery);
  border-radius: vw(6);

  @include medium {
    border-radius: md-vw(6);
  }
}

.choices__item--choice {
  border-radius: none;
  border: none;
}

.choices .is-selected {
  display: none;
}

.choices__placeholder {
  opacity: 1;
}

.choices[data-type*=select-one]:after {
  border: none;
  width: vw(8);
  height: vw(8);
  border-bottom: vw(1) solid var(--color-text-gallery);
  border-left: vw(1) solid var(--color-text-gallery);
  transform: rotate(-45deg) translateY(-100%);
  margin-top: 0;

  @include medium {
    width: md-vw(8);
    height: md-vw(8);
    border-bottom: md-vw(1) solid var(--color-text-gallery);
    border-left: md-vw(1) solid var(--color-text-gallery);
  }
}

.choices[data-type*=select-one].is-open:after {
  border: none;
  width: vw(8);
  height: vw(8);
  border-bottom: vw(1) solid var(--color-text-gallery);
  border-left: vw(1) solid var(--color-text-gallery);
  transform: rotate(-45deg) translateY(-100%);
  margin-top: 0;

  @include medium {
    width: md-vw(8);
    height: md-vw(8);
    border-bottom: md-vw(1) solid var(--color-text-gallery);
    border-left: md-vw(1) solid var(--color-text-gallery);
  }
}

.choices__list {
  padding: 0;
}

.choices__inner {
  display: flex;
  align-items: center;
  padding: 0;
  border: none;
}

.is-open .choices__inner {
  border-radius: vw(6);

  @include medium {
    border-radius: md-vw(6);
  }
}

.is-focused .choices__inner, .is-open .choices__inner{
  border: none;
}

.choices[data-type*=select-one] .choices__inner {
  padding-bottom: 0;
}

.choices__list--dropdown .choices__item--selectable:after {
  content: '';
}

.choices__list--dropdown .choices__item--selectable.is-highlighted {
  background: transparent;
}

.choices__list--dropdown {
  border: none;
  border-radius: vw(6);
  border: vw(1) solid var(--color-border-select-gallery);
  margin-top: vw(-3);

  @include medium {
    border-radius: md-vw(6);
    border: md-vw(1) solid var(--color-border-select-gallery);
    margin-top: md-vw(-3);
  }

  .choices__item {
    color: var(--color-border-select-gallery);
    border-radius: 0;
  }
}

.is-open .choices__list--dropdown {
  border: vw(1) solid var(--color-border-select-gallery);

  @include medium {
    border: md-vw(1) solid var(--color-border-select-gallery);
  }
}

.choices__list--dropdown .choices__item:not(:last-child) {
  border-bottom: vw(1) solid var(--color-border-select-gallery);

  @include medium {
    border-bottom: md-vw(1) solid var(--color-border-select-gallery);
  }
}




.gallery__right-group {
  position: relative;

  width: 100%;
  max-width: vw(1150);
  height: 100%;
  max-height: vw(810);
  padding-top: vw(60);

  @include medium {
    max-width: md-vw(1150);
    height: 100%;
    max-height: md-vw(650);
    padding-top: md-vw(60);
  }
}

.gallery__swiper-container {
  width: 100%;
  max-width: vw(1150);
  height: 100%;

  @include medium {
    max-width: md-vw(580);
  }
}

.gallery__swiper-slide {
  width: vw(350);
  height: vw(350);

  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: center center;

  @include medium {
    width: md-vw(280);
    height: md-vw(280);
  }
}

.gallery__swiper-button-prev,
.gallery__swiper-button-next {
  position: absolute;
  top: 0;
  left: 0;
  min-width: vw(30);
  width: vw(30);
  min-height: vw(30);
  height: vw(30);
  background: var(--color-arrow-gallery);
  border-radius: 50%;
  z-index: 1000;
  cursor: pointer;

  &::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-35%, -50%) rotate(45deg);
    width: vw(8);
    height: vw(8);

    border-bottom: vw(1) solid var(--color-text-gallery);
    border-left: vw(1) solid var(--color-text-gallery);

    @include medium {
      width: md-vw(8);
      height: md-vw(8);

      border-bottom: md-vw(1) solid var(--color-text-gallery);
      border-left: md-vw(1) solid var(--color-text-gallery);
    }
  }

  @include medium {
    min-width: md-vw(30);
    width: md-vw(30);
    min-height: md-vw(30);
    height: md-vw(30);
  }
}

.gallery__swiper-button-next {
  left: vw(100);

  &::after {
    transform: translate(-65%, -50%) rotate(-135deg);
  }

  @include medium {
    left: md-vw(100);
  }
}

.gallery__swiper__count {
  position: absolute;
  top: 0;
  left: vw(50);
  font-size: vw(16);
  line-height: vw(32);
  color: var(--color-text-gallery);

  @include medium {
    left: md-vw(50);
    font-size: md-vw(16);
    line-height: md-vw(32);
  }
}


