.hero {
  position: relative;
  width: 100%;
  min-height: vw(700);

  @include medium {
    min-height: md-vw(668);
  }
}

.hero__topbar {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;

  height: vw(70);
  background-color: var(--color-bg-topbar-hero);

  z-index: 101;

  @include medium {
    display: none;
  }
}

.hero__topbar__container {
  display: flex;
  align-items: center;

  height: 100%;
}

.hero__btn {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;

  height: 100%;

  font-size: vw(18);
  line-height: vw(25);
  color: var(--color-text-hero);

  padding-right: vw(13);

  &::after {
    content: '';
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-60%) rotate(-45deg);

    width: vw(8);
    height: vw(8);
    
    border-bottom: vw(1) solid var(--color-text-hero);
    border-left: vw(1) solid var(--color-text-hero);
  }

  &:not(:last-of-type) {
    margin-right: vw(96);
  }
}

.hero__dropdown {
  position: absolute;
  top: vw(70);
  left: 0;

  width: vw(300);

  background: var(--color-bg-dropdown-hero);
  padding: vw(40) vw(19) vw(40) vw(40);

  opacity: 0;
  visibility: hidden;
  height: 0;
  overflow: hidden;

  transition: opacity .3s ease-in-out, visibility .3s ease-in-out, height .3s ease-in-out;
}

.hero__btn.is_active {
  .hero__dropdown {
    opacity: 1;
    visibility: visible;
    height: vw(300);
  }
}

.hero__list {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;

  padding-right: vw(26);

  width: 100%;
  max-width: 100%;
  height: 100%;

  overflow-y: auto;
  overflow-x: hidden;

  .simplebar-scrollbar::before {
    background-color: var(--color-text-hero);
    opacity: 1;
    width: vw(2);
    border-radius: 0;
    z-index: 999999;
  }

  &::after {
    content: '';
    position: absolute;
    right: 0;
    top: vw(2);
    bottom: 0;
    width: vw(2);
    height: 98%;
    background: var(--color-bg-scrollbar-hero);
  }

  .simplebar-vertical {
    right: vw(-7);
  }

  /* ,  */
  &::-webkit-scrollbar { width: 0; }

  /* ie 10+ */
  & { -ms-overflow-style: none; }

  /*  (   ,    )*/
  & { overflow: -moz-scrollbars-none; }
}

.hero__item {
  display: flex;
  align-items: center;
  justify-content: center;

  width: 100%;
  height: vw(40);
  min-height: vw(40);
  max-height: vw(40);

  padding-right: vw(19);

  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: center center;

  &:not(:last-of-type) {
    margin-bottom: vw(20);
  }
}

