.hero__topbar__search {
  display: flex;
  align-items: center;

  margin-left: auto;

  .hero__topbar__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: vw(22.1);
    height: vw(21.62);

    margin-right: vw(9);
    transform: translateY(vw(2));

    svg {
      width: vw(22.1);
      height: vw(21.62);
  
      fill: var(--fill-svg-hero);
    }
  }
}

.hero__topbar__input {
  background: transparent;
  border: none;
  outline: none;

  width: vw(218);
  border-bottom: vw(1) solid var(--color-search-hero);

  font-size: vw(18);
  line-height: vw(25);
  color: var(--color-text-hero);

  &::placeholder {
    font-size: vw(18);
    line-height: vw(25);
    color: var(--color-search-hero);
  }
}




.hero__swiper-container {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.hero__swiper-slide {
  overflow: hidden;
}

.hero__swiper-img {
  width: 100%;
  height: 100%;

  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: bottom center;

  animation: heroBgScaleAnim 10s alternate infinite ease-in-out;
}

@keyframes heroBgScaleAnim {
  0% {
    transform: scale(1);
  }

  100% {
    transform: scale(1.25);
  }
}

