.hero__main-heading {
  position: relative;
  max-width: vw(597);

  font-weight: 700;
  font-size: vw(72);
  line-height: 120%;
  color: var(--color-text-hero);
  margin-bottom: vw(15);
  
  padding-top: vw(133);
  margin-top: 0;
  z-index: 100;

  @include medium {
    max-width: md-vw(597);

    font-size: md-vw(72);
    margin-bottom: md-vw(1);
    
    padding-top: md-vw(105);
  }
}

.hero__desc {
