    line-height: md-vw(30);

    margin-bottom: md-vw(92);
  }
}

.hero__main-btn {
  position: relative;

  padding: vw(22.5) vw(35);
  border-radius: vw(100);
  background: var(--color-main);

  font-weight: 700;
  font-size: vw(18);
  line-height: 100%;
  color: var(--color-text-hero);

  z-index: 100;

  @include medium {
    padding: md-vw(22.5) md-vw(35);
    border-radius: md-vw(100);
    background: var(--color-main);

    font-weight: 700;
    font-size: md-vw(18);
  }
}