.events {
  padding-bottom: vw(80);

  @include medium {
    padding-bottom: md-vw(80);
  }
}

.events__container {
  display: flex;
  flex-direction: column;
}

.events__heading {
  transition: padding-top .15s ease;
}


.events__list {
  display: grid;
  grid-template-columns: repeat(3, vw(500));
  grid-gap: vw(50);

  width: 100%;
  margin-bottom: vw(50);

  .events__item:not(:nth-child(-n+3)) {
    display: none;
  }

  @include medium {
    grid-template-columns: repeat(3, md-vw(290));
    grid-gap: md-vw(27);

    margin-bottom: md-vw(27);
  }
}

.events__list.is_active {
  .events__item:not(:nth-child(1)),
  .events__item:not(:nth-child(2)),
  .events__item:not(:nth-child(3)) {
    display: block;
  }
}

.events__item {
  position: relative;

  width: 100%;
  min-height: vw(700);
  padding: vw(50);
  padding-top: 0;

  border: vw(1) solid var(--color-card-border-events);

  @include medium {
    min-height: md-vw(683);
    padding: md-vw(35);
    padding-top: 0;

    border: md-vw(1) solid var(--color-card-border-events);
  }
}

.events__item__img {
  width: vw(500);
  height: vw(300);
  margin-left: vw(51) * -1;
  margin-right: vw(51) * -1;
  margin-top: vw(1) * -1;

  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;

  margin-bottom: vw(50);

  @include medium {
    width: md-vw(290);
    height: md-vw(200);
    margin-left: md-vw(36) * -1;
    margin-right: md-vw(36) * -1;
    margin-top: md-vw(1) * -1;
    margin-bottom: md-vw(50);
  }
}

.events__item__img.eii1 {
  background-image: url('../img/events/1.png');
}
.events__item__img.eii2 {
  background-image: url('../img/events/2.png');
}
.events__item__img.eii3 {
  background-image: url('../img/events/3.png');
}
.events__item__img.eii4 {
  background-image: url('../img/events/4.png');
}
.events__item__img.eii5 {
  background-image: url('../img/events/5.png');
}

.events__item__heading {
  font-weight: 600;
  font-size: vw(24);
  line-height: vw(33);
  color: var(--color-text-events);

  margin-bottom: vw(10);

  @include medium {
    font-size: md-vw(24);
    line-height: md-vw(33);
    color: var(--color-text-events);

    margin-bottom: md-vw(10);
  }
}

.events__item__desc {
  font-size: vw(16);
  line-height: vw(32);
  color: var(--color-text-events);

  padding-bottom: vw(30);

  @include medium {
    font-size: md-vw(16);
    line-height: md-vw(32);

    padding-bottom: md-vw(30);
  }
}

.events__item__more {
  position: absolute;
  left: vw(50);
  bottom: vw(50);

  font-size: vw(16);
  line-height: vw(32);
  color: var(--color-more-events);
  border-bottom: vw(1) solid var(--color-more-events);

  @include medium {
    left: md-vw(35);
    bottom: md-vw(45);

    font-size: md-vw(16);
    line-height: md-vw(32);
    border-bottom: md-vw(1) solid var(--color-more-events);
  }
}

.events__item__place {
  position: absolute;
  top: vw(313);
  right: vw(19);

  max-width: vw(211);

  font-size: vw(12);
  line-height: vw(16);
  color: var(--color-where-events);
  text-align: right;

  @include medium {
    top: md-vw(214);
    right: md-vw(18);

    max-width: md-vw(211);

    font-size: md-vw(12);
    line-height: md-vw(16);
  }
}

.events__item:nth-child(1) .events__item__place::first-letter {
  padding-left: vw(100);

  @include medium {
    padding-left: md-vw(100);
  }
}
.events__item:nth-child(2) .events__item__place::first-letter {
  padding-left: vw(150);

  @include medium {
    padding-left: md-vw(150);
  }
}
.events__item:nth-child(3) .events__item__place::first-letter {
  padding-left: vw(105);

  @include medium {
    padding-left: md-vw(150);
  }
}
.events__item:nth-child(4) .events__item__place::first-letter {
  padding-left: vw(25);

  @include medium {
    padding-left: md-vw(25);
  }
}
.events__item:nth-child(5) .events__item__place::first-letter {
  padding-left: vw(170);

  @include medium {
    padding-left: md-vw(25);
  }
}



.events__btn {
  display: inline-block;

  font-weight: 700;
  font-size: vw(18);
  line-height: vw(25);
  color: var(--color-more-events);

  padding: vw(17) vw(35);
  border-radius: vw(100);
  border: vw(2) solid var(--color-more-events);

  align-self: center;

  @include medium {
    font-size: md-vw(18);
    line-height: md-vw(25);

    padding: md-vw(17) md-vw(35);
    border-radius: md-vw(100);
    border: md-vw(2) solid var(--color-more-events);
  }
}