.header {
  position: relative;
  z-index: 100;
  width: 100%;
  height: vw(100);
  
  background: var(--color-bg-header);

  @include medium {
    height: md-vw(100);
  }

  @include small {
    height: sm-vw(100);
  }
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;

  height: 100%;
}

.header__menu {
  display: none;
  align-items: flex-start;
  justify-content: space-between;
  flex-direction: column;

  width: md-vw(36);
  height: md-vw(28);

  span {
    background: var(--color-bg);
    width: 100%;
    height: md-vw(3.65);

    @include small {
      height: sm-vw(3.65);
    }

    &:nth-child(2) {
      width: md-vw(28.2);

      @include small {
        width: sm-vw(28.2);
      }
    }

    &:nth-child(3) {
      width: md-vw(24);

      @include small {
        width: sm-vw(24);
      }
    }
  }

  @include medium {
    display: flex;
  }

  @include small {
    width: sm-vw(36);
    height: sm-vw(28);
  }
}

.header__logo {
  width: vw(173);
  height: vw(39.9);
  
  svg {
    width: vw(173);
    height: vw(39.9);

    fill: var(--fill-svg-header);

    @include medium {
      width: md-vw(187.6);
      height: md-vw(42);
    }

    @include small {
      width: sm-vw(187.6);
      height: sm-vw(42);
    }
  }

  @include medium {
    width: md-vw(187.6);
    height: md-vw(42);
  }
}

.nav {
  height: 100%;
  margin-right: vw(38);

  @include medium {
    display: none;
  }
}

.nav__list {
  display: flex;
  align-items: center;

  height: 100%;
}

.nav__item {
  .nav__link {
    font-weight: 600;
    font-size: vw(18);
    line-height: vw(25);
    color: var(--color-text-header);
    padding: vw(31);
  }
}

.header__profile {
  font-size: vw(18);
  line-height: vw(25);
  color: var(--color-text-header);

  svg {
    width: vw(13);
    height: vw(13);

    fill: var(--fill-svg-header);
    margin-right: vw(7);
  }

  @include medium {
    display: none;
  }
}

.header__search {
  display: none;

  @include medium {
    display: inline-block;
    width: md-vw(29.45);
    height: md-vw(28.82);
  }

  svg {
    width: md-vw(29.45);
    height: md-vw(28.82);

    fill: var(--fill-svg-header);
  }
}