.projects {
  padding: vw(80) 0;

  @include medium {
    padding: md-vw(80) 0;
  }
}

.projects__desc {
  max-width: vw(1080);

  margin-bottom: vw(30);

  @include medium {
    max-width: md-vw(925);

    margin-bottom: md-vw(30);
  }
}

.projects__desc__link {
  font-weight: 700;
  color: var(--color-main);
}

.projects__swiper-heading {
  font-weight: 600;
  font-size: vw(24);
  line-height: vw(32);
  color: var(--color-text-projects);

  margin-bottom: vw(23);

  @include medium {
    font-size: md-vw(24);
    line-height: md-vw(32);

    margin-bottom: md-vw(23);
  }
}


.projects__swiper {
  position: relative;

  padding: 0 vw(75);

  @include medium {
    padding: 0 md-vw(55);
  }
}

.projects__swiper-container {
  max-width: vw(1450);

  @include medium {
    max-width: vw(1450);
  }
}

.projects__swiper-slide {
  display: flex;
  align-items: center;
  justify-content: center;

  width: vw(450);
  height: vw(150);
  border: vw(1) solid var(--color-card-border-projects);

  cursor: pointer;
  
  @include medium {
    width: md-vw(384);
    height: md-vw(128);
    border: md-vw(1) solid var(--color-card-border-projects);
  }
}

.projects__swiper-slide__img {
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: center center;

  filter: grayscale(1);
  opacity: .3;
}

