.projects__swiper-button-prev,
.projects__swiper-button-next {
  position: absolute;
  top: vw(60);
  left: 0;
  min-width: vw(30);
  width: vw(30);
  min-height: vw(30);
  height: vw(30);
  background: var(--color-arrow-projects);
  border-radius: 50%;
  z-index: 1000;
  cursor: pointer;

  @include medium {
    top: md-vw(60);
    min-width: md-vw(30);
    width: md-vw(30);
    min-height: md-vw(30);
    height: md-vw(30);  
  }

  &::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-30%, -50%) rotate(45deg);
    width: vw(8);
    height: vw(8);

    border-bottom: vw(1) solid var(--color-black);
    border-left: vw(1) solid var(--color-black);

    @include medium {
      width: md-vw(8);
      height: md-vw(8);

      border-bottom: md-vw(1) solid var(--color-black);
      border-left: md-vw(1) solid var(--color-black);
    }
  }
}

.projects__swiper-button-next {
  right: 0;
  left: auto;

  &::after {
    transform: translate(-70%, -50%) rotate(-135deg);
  }
}